@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
body {
  margin: 0;
  font-family: 'Source Code Pro', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scrollbar-width: none;
  color: black;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 0px;  
  background: transparent;  
}

a {
  text-decoration: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.menu-button{position:relative;text-decoration:none;color:#64D7FF}.menu-button:before{content:"";position:absolute;width:100%;height:2px;bottom:0;left:0;background-color:#64D7FF;visibility:hidden;transform:scaleX(0);transition:all 0.3s ease-in-out 0s}.menu-button:hover:before{visibility:visible;transform:scaleX(1)}.active{color:#FFF000}

.container{position:relative;width:24px;height:24px}.chevron{position:absolute;width:28px;height:8px;opacity:0;transform:scale3d(0.5, 0.5, 0.5);-webkit-animation:move 3s ease-out infinite;animation:move 3s ease-out infinite}.chevron:first-child{-webkit-animation:move 3s ease-out 1s infinite;animation:move 3s ease-out 1s infinite}.chevron:nth-child(2){-webkit-animation:move 3s ease-out 2s infinite;animation:move 3s ease-out 2s infinite}.chevron:before,.chevron:after{content:' ';position:absolute;top:0;height:100%;width:51%;background:#fff}.chevron:before{left:0;transform:skew(0deg, 30deg)}.chevron:after{right:0;width:50%;transform:skew(0deg, -30deg)}@-webkit-keyframes move{25%{opacity:1}33%{opacity:1;transform:translateY(30px)}67%{opacity:1;transform:translateY(40px)}100%{opacity:0;transform:translateY(55px) scale3d(0.5, 0.5, 0.5)}}@keyframes move{25%{opacity:1}33%{opacity:1;transform:translateY(30px)}67%{opacity:1;transform:translateY(40px)}100%{opacity:0;transform:translateY(55px) scale3d(0.5, 0.5, 0.5)}}@-webkit-keyframes pulse{to{opacity:1}}@keyframes pulse{to{opacity:1}}.texts{display:block;font-family:"Helvetica Neue", "Helvetica", Arial, sans-serif;font-size:12px;color:#fff;text-transform:uppercase;white-space:nowrap;opacity:.25;-webkit-animation:pulse 2s linear alternate infinite;animation:pulse 2s linear alternate infinite}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 2.5em;
    pointer-events: none;
}

@-webkit-keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
  
